<template>
    <div></div>
</template>

<script>

    import Data from '../assets/data/items.json';

    export default {
        name:"ItemsList",
        props: ["items"],
        methods:{
            getItemsList(){
                return this.$emit('loadItems', Data)
            }
        },
        created(){
            this.getItemsList()
        }
    }
</script>

